import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { signIn, getMeals, getActivities } from "../../actions";
import { Link } from "react-router-dom";

class Login extends React.Component {
  componentDidMount() {}

  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="ui mini red message">{error}</div>;
    }
  }

  onSubmit = formValues => {
    this.props.signIn(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    if (input.name === "password" || input.name === "password2") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" />
          {this.renderError(meta)}
        </div>
      );
    } else {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" />
          {this.renderError(meta)}
        </div>
      );
    }
  };

  checkUser = () => {
    return (
      <div className="column form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Wprowadź login"
          />
          <Field
            name="password"
            component={this.renderInput}
            label="Wprowadź hasło"
          />

          <button className="ui button primary">Zaloguj</button>
          <Link to="/changepassword" className="ui button ">
            Zapomniałeś hasła?
          </Link>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div className="ui red message">Zalogowany</div>;
    } else {
      return (
        <div id="loginAll">
          <div id="login">{this.checkUser()}</div>
          <div id="appInfo">
          <h4>Wszystkie kalorie w jednym miejscu!</h4>

            <div id="description">
              <div className="text">Zaawansowany dzienniczek kaloryczny dla spożytych oraz spalonych kalorii.</div>
              <b>Po co ?</b>
              <div className="text">Śledzenie kalorii nigdy nie było tak łatwe.</div>
              <b>Raporty żywieniowe</b>
              <div className="text">Generujesz raport żywieniowy dla dowolnie wybranego przez Ciebie okresu.</div>
              <b>Jak to działa ?</b>
              <img src="./images/information.png" alt="Information"/>
            </div>
          </div>
        </div>
      );
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};

const validate = formValues => {
  const errors = {};

  if (!formValues.username) {
    errors.username = "Musisz podać login";
  }

  if (!formValues.password) {
    errors.password = "Musisz podać hasło";
  }

  return errors;
};

const formWrapped = reduxForm({
  form: "login",
  validate: validate
})(Login);

export default connect(
  mapStateToProps,
  { signIn, getMeals, getActivities }
)(formWrapped);
