import React from "react";
import { connect } from "react-redux";
import { addMeal } from "../../actions";
import history from "../../history";
import { Field, reduxForm } from "redux-form";
import "react-widgets/dist/css/react-widgets.css";
import { DateTimePicker } from "react-widgets";

import Moment from "moment";
import momentLocalizer from "react-widgets-moment";

Moment.locale("en");
momentLocalizer();

class AddMeal extends React.Component {
  state = { startDate: new Date() };
  componentDidMount() {}

  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="ui mini red message">{error}</div>;
    }
  }

  onSubmit = formValues => {
    this.props.addMeal(formValues);
  };

  renderInputDate = ({ input: { onChange }, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    return (
      <div className={className}>
        <label>{label}</label>
        <DateTimePicker time={false} onChange={onChange} />
        {this.renderError(meta)}
      </div>
    );
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} autoComplete="off" />
        {this.renderError(meta)}
      </div>
    );
  };

  renderMealType = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    return (
      <div className={className}>
        <label>{label}</label>
        <select {...input}>
          <option />
          <option value="śniadanie">śniadanie</option>
          <option value="lunch">lunch</option>
          <option value="obiad">obiad</option>
          <option value="podwieczorek">podwieczorek</option>
          <option value="kolacja">kolacja</option>
          <option value="przekąska">przekąska</option>
        </select>
        {this.renderError(meta)}
      </div>
    );
  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error"
        >
          <Field
            name="meal"
            component={this.renderMealType}
            label="Wprowadź posiłek"
          />
          <Field
            name="kcal"
            component={this.renderInput}
            label="Zjedzone kalorie"
          />
          <Field
            name="date"
            showTime={false}
            component={this.renderInputDate}
            label="Wprowadź datę"
          />
          <Field
            name="description"
            component={this.renderInput}
            label="Wprowadź opis"
          />

          <button className="ui button primary">Dodaj</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div id="meals">{this.checkUser()}</div>;
    } else {
      history.push("/login");
      return <div>Niezalogowany</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    meals: Object.values(state.meals)
  };
};

const validate = formValues => {
  const errors = {};

  if (!formValues.meal) {
    errors.meal = "Musisz podać jakiś posiłek";
  }

  if (!formValues.kcal) {
    errors.kcal = "Musisz podać kalorie";
  }

  if (!formValues.date) {
    errors.date = "Musisz podać datę";
  }

  if (isNaN(parseInt(formValues.kcal))) {
    errors.kcal = "Kalorie wyraża się w liczbach";
  }

  return errors;
};

const formWrapped = reduxForm({
  form: "addmeal",
  validate: validate
})(AddMeal);

export default connect(
  mapStateToProps,
  { addMeal }
)(formWrapped);
