import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { changePassword, sendEmail } from "../../actions";
import history from "../../history";

class ChangePassword extends React.Component {
  componentDidMount() {}

  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="ui mini red message">{error}</div>;
    }
  }

  onSubmit = formValues => {
    this.props.changePassword(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    if (input.name === "newpassword" ) {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" />
          {this.renderError(meta)}
        </div>
      );
    } else {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" />
          {this.renderError(meta)}
        </div>
      );
    }
  };

  sendEmail = formValues => {
    this.props.sendEmail(formValues);
  };

  checkUser = () => {
    return (
      <div className="column form-container">
        <form
          onSubmit={this.props.handleSubmit(this.sendEmail)}
          className="ui form error sendmail"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Wprowadź nazwę użytkownika"
          />

          <button className="ui button violet">Wyślij maila</button>
        </form>

        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error changepassword"
        >
          <Field
            name="code"
            component={this.renderInput}
            label="Wprowadź kod z wiadomości email"
          />
          <Field
            name="newpassword"
            component={this.renderInput}
            label="Wprowadź nowe hasło"
          />

          <button className="ui button grey">Zmień hasło</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      history.push("/");
      return <div>Zalogowany</div>;
    } else {
      return <div id="changePassword">{this.checkUser()}</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};

const validate = formValues => {
  const errors = {};

  if (!formValues.username) {
    errors.username = "Musisz podać login";
  }

  return errors;
};

const formWrapped = reduxForm({
  form: "changepassword",
  validate: validate
})(ChangePassword);

export default connect(
  mapStateToProps,
  { changePassword, sendEmail }
)(formWrapped);
