import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { signUp } from "../../actions";

class Register extends React.Component {
  componentDidMount() {}

  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="ui mini red message">{error}</div>;
    }
  }

  onSubmit = formValues => {
    this.props.signUp(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    if (input.name === "password" || input.name === "password2") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" />
          {this.renderError(meta)}
        </div>
      );
    } else {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" />
          {this.renderError(meta)}
        </div>
      );
    }
  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Wprowadź login"
          />
          <Field
            name="email"
            component={this.renderInput}
            label="Wprowadź email"
          />
          <Field
            name="name"
            component={this.renderInput}
            label="Wprowadź imię"
          />
          <Field
            name="password"
            component={this.renderInput}
            label="Wprowadź hasło"
          />
          <Field
            name="password2"
            component={this.renderInput}
            label="Wprowadź hasło ponownie"
          />
          <button className="ui button primary">Zarejestruj</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div className="ui red message">Zalogowany</div>;
    } else {
      return <div id="register">{this.checkUser()}</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};

const validate = formValues => {
  const errors = {};

  if (!formValues.username) {
    errors.username = "Musisz podać login";
  }

  if (!formValues.email) {
    errors.email = "Musisz podać email";
  }

  if (!formValues.password) {
    errors.password = "Musisz podać hasło";
  }

  if (!formValues.name) {
    errors.name = "Musisz podać imię";
  }

  if (!formValues.password2) {
    errors.password2 = "Musisz podać hasło drugi raz";
  }

  if (!/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(formValues.email)) {
    errors.email = "Musisz podać poprawny adres email";
  }

  return errors;
};

const formWrapped = reduxForm({
  form: "register",
  validate: validate
})(Register);

export default connect(
  mapStateToProps,
  { signUp }
)(formWrapped);
