import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { logOut } from "../../actions";

class Header extends React.Component {
  onLogOutClick = () => {
    this.props.logOut();
  };

  componentDidMount() {}

  componentWillReceiveProps(nextProps) {}

  onLogOff = () => {
    return (
      <div className="ui buttons header-buttons">
        <Link to="/login" className="ui button">
          Logowanie
        </Link>
        <div className="or" />
        <Link to="/register" className="ui positive button">
          Rejestracja
        </Link>
      </div>
    );
  };

  onLogIn = () => {
    return (
      <div className="ui buttons header-buttons">
        <Link to="/bmr" className="ui button">
          BMR
        </Link>

        <div className="or" />

        <Link
          to="/login"
          className="ui teal button"
          onClick={this.onLogOutClick}
        >
          Wyloguj({this.props.user.user.username})
        </Link>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div className="header">{this.onLogIn()}</div>;
    } else {
      return <div className="header">{this.onLogOff()}</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    meals: state.meals,
    activities: state.activities
  };
};

export default connect(
  mapStateToProps,
  { logOut }
)(Header);
