import React from "react";
import { connect } from "react-redux";
import { addActivity } from "../../actions";
import history from "../../history";
import { Field, reduxForm } from "redux-form";
import "react-widgets/dist/css/react-widgets.css";
import { DateTimePicker } from "react-widgets";

import Moment from "moment";
import momentLocalizer from "react-widgets-moment";

Moment.locale("en");
momentLocalizer();

class AddActivity extends React.Component {
  state = { startDate: new Date() };
  componentDidMount() {}

  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="ui mini red message">{error}</div>;
    }
  }

  onSubmit = formValues => {
    this.props.addActivity(formValues);
  };

  renderInputDate = ({ input: { onChange }, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    return (
      <div className={className}>
        <label>{label}</label>
        <DateTimePicker time={false} onChange={onChange} />
        {this.renderError(meta)}
      </div>
    );
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field ${meta.error && meta.touched ? "error" : ""}`;

    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} autoComplete="off" />
        {this.renderError(meta)}
      </div>
    );
  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error"
        >
          <Field
            name="activity"
            component={this.renderInput}
            label="Nazwa aktywności"
          />
          <Field
            name="kcal"
            component={this.renderInput}
            label="Spalone kalorie"
          />
          <Field
            name="date"
            showTime={false}
            component={this.renderInputDate}
            label="Wprowadź datę"
          />
          <Field
            name="description"
            component={this.renderInput}
            label="Wprowadź opis"
          />

          <button className="ui button primary">Zatwierdź</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div id="bmr">{this.checkUser()}</div>;
    } else {
      history.push("/login");
      return <div>Niezalogowany</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    activities: Object.values(state.activities)
  };
};

const validate = formValues => {
  const errors = {};

  if (!formValues.activity) {
    errors.activity = "Musisz podać jakiś posiłek";
  }

  if (!formValues.kcal) {
    errors.kcal = "Musisz podać kalorie";
  }

  if (!formValues.date) {
    errors.date = "Musisz podać datę";
  }
  if (isNaN(parseInt(formValues.kcal))) {
    errors.kcal = "Kalorie wyraża się w liczbach";
  }
  return errors;
};

const formWrapped = reduxForm({
  form: "addactivity",
  validate: validate
})(AddActivity);

export default connect(
  mapStateToProps,
  { addActivity }
)(formWrapped);
